import { watchScreen, insertCSS } from "utils";

// Switches tabs containers and applies active class.
// USAGE:


const css = mobileBreakpoint => `
  
  .pie-tabs-hide-on-desktop {
    display: none;
  }
  
  @media (max-width: ${mobileBreakpoint}px) {
    .pie-tabs-container {
      top: 100%;     
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0;
      z-index: 100;
      position: fixed;
      overflow-y: auto;
      overflow-x: hidden;
      background-color: white;
      transition: top .5s ease;
    }
      
    .pie-tabs-container.show {
      top: 3.4rem;
    } 

    .pie-tabs-hide-on-mobile {
      display: none;
    }

    .pie-tabs-hide-on-desktop {
      display: flex;
    }
  }

  .pie-tabs-container > *[data-tab] {
    display: none;
  }

  .pie-tabs-container > *[data-tab].active {
    display: block;
  }      
`;

export default function TabsManager(mobileBreakpoint = 860) {
  let tabs = Array.from(document.querySelectorAll("[data-tab]"));
  let container = document.querySelector(".pie-tabs-container");
  let currentTab = tabs.find(t => t.classList.contains("active"));
  let currentButton = currentTab ? document.querySelector(`button[data-tab-target].active`) : null;

  insertCSS(css(mobileBreakpoint), "tabs-styles");

  watchScreen(
    width => ({ mobile: width < mobileBreakpoint }),
    mode => currentButton && (mode === "mobile"
      ? currentButton.classList.remove("active")
      : currentButton.classList.add("active"))
    , true
  );

  function deselectTabs() {
    container && container.classList.remove("show");
    currentButton && currentButton.classList.remove("active");
  }

  function selectTab(targetName, button) {

    currentButton && currentButton.classList.remove("active");
    currentButton = button instanceof HTMLElement ? button : document.querySelector(`button[data-tab-target="${targetName}"]`);

    currentTab && currentTab.classList.remove("active");
    currentTab = tabs.find(t => t.dataset.tab === targetName);

    if (currentTab) {
      currentTab.classList.add("active");
      currentButton.classList.add("active");
      container && container.classList.add("show");
    }

    return currentTab;
  }

  document.addEventListener("click", event => {
    if (event.target.matches("button[data-tab-target]")) {
      currentTab = selectTab(event.target.dataset.tabTarget, event.target);
    }

    else if (event.target.matches("button[data-tab-close]")) {
      deselectTabs();
    }
  });

  return Object.freeze({
    selectTab,

    getCurrentTab() {
      return currentTab;
    },

    getCurrentButton() {
      return currentButton;
    },
  });
}
