// Debounce (delay) callback fn.
export function debounce(callback, delay, immediate) {
  let timeout;
  return (...args) => {
    const later = () => {
      timeout = null;
      !immediate && callback(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, delay);
    immediate && !timeout && callback(...args);
  };
}

// Extract search query from URL and parse it into JS object.
// Returns: {} OR {key:value, ...};
export function queryToObject(url) {
  const searchParams = new URLSearchParams(url);
  const result = {};
  for (const [key, value] of searchParams.entries()) {
    if (result[key]) {
      if (!Array.isArray(result[key])) {
        result[key] = [result[key]]
      }
      result[key].push(value);
    } else {
      result[key] = value;
    }
  }
  return result;
}

// Geerate unique id.
export const uid = () =>
  `PIE.${(+new Date() + Math.random() * 100).toString(32)}`;

// Modifier function with memory.
export const memo = (modifier, previous) => {
  if (typeof modifier !== "function") throw "Modifier need to be a function.";
  return current => {
    // NOTE: To memoize previous value you need to return it from the 'modifier'.
    previous = modifier(current, previous);
    return previous;
  };
};

// Memoize prev. value.
export function isPrevious(state) {
  return newState => {
    if (state !== newState) {
      state = newState;
      return false;
    }
    return true;
  };
}

// Translate clock time notation into seconds.
export function timeToSeconds(time) {
  const parts = time.split(":");
  return (
    parseInt(parts[0]) * 60 * 60 + parseInt(parts[1]) * 60 + parseInt(parts[2])
  );
}

// Add <style> tag into Document's head.
export function insertCSS(styles, name, override = false) {
  const styleTag = override
    ? document.querySelector(`style[data-name="${name}"]`) ||
    document.createElement(`style`)
    : document.createElement(`style`);
  if (name) styleTag.dataset.name = name;
  document.head.appendChild(styleTag).textContent = styles;
  return styleTag;
}

// Create pistacja Error instance.
export function PieError(error, host) {
  this.name = "PieError";
  if (error instanceof Error) {
    this.message = error.message;
    this.stack = error.stack;
  } else {
    this.message = error;
    this.stack = new Error().stack;
  }

  if (host) {
    this.host = host;
  }
}

PieError.prototype = new Error();

// Toggles given @klass on given @newElement removing its from the previouse one.
// USAGE:
// const avtivate = memoElement(htmlElement, "avtive");
// ...
// avtivate(newHtmlElement);
//
export function memoElement(currentElement, klass, toggle = false) {
  currentElement && currentElement.classList.add(klass);
  return newElement => {
    if (toggle && currentElement && currentElement === newElement) {
      currentElement.classList.toggle(klass);
    } else {
      currentElement && currentElement.classList.remove(klass);
      newElement && newElement.classList.add(klass);
      // eslint-disable-next-line no-param-reassign
      currentElement = newElement;
    }
    return currentElement;
  };
}


// Runs callback only id screen mode changes.
// USAGE:
/*

import { watchScreen } from "utils";
. . .

watchScreen(width => ({ 
  mobile: width < 450, 
  tablet: width < 860 && width >= 450,
}), 
  (mode, isInitail) => console.log(mode, isInitail), 
  true
);

*/


export function watchScreen(predicate, callback, emmitOnInit = false) {

  let prevMode;

  const handler = debounce(() => {
    const screenWidth = window.innerWidth;
    const screenHeight = window.innerHeight;
    const { mobile, tablet } = predicate(screenWidth, screenHeight);
    const mode = mobile ? "mobile" : tablet ? "tablet" : "desktop";

    if (prevMode !== mode) {
      callback(mode, prevMode === undefined);
      prevMode = mode;
    }

  }, 450);

  emmitOnInit && handler();

  window.addEventListener("resize", handler);
  return () => window.removeEventListener("resize", handler);
} 